.task {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: .5rem 1.5rem;
}

.task:not(:last-child) {
  border-bottom: 1px solid var(--color-gray-lighter);
}

.task__checkbox {
  margin: 0 1rem 0 0;
}

.task__title {
  margin-right: auto;
}

.task__title--is-completed {
  text-decoration: line-through;
  color: var(--color-gray);
}

.task__deadline {
  text-transform: uppercase;
  font-size: 85%;
  color: var(--color-gray);
}

.task__button {
  border: none;
  padding: 0;
  margin: 0 0 0 1rem;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  line-height: 1;
  background-color: transparent;
  outline: none;
  color: var(--color-gray-darker);
}

.task__button:hover {
  color: var(--color-gray-darkest);
}


